.appDiv{
    width: 100vw;
    background-color: lightcoral;
    display: flex;
    flex-direction: column;
    align-items: center;
}
nav{
    background-color: coral;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid black;
    padding: 0 15px;
    box-shadow: 0px 1px 8px 3px rgba(115,106,106,1);
}

p{
    font-family: 'Oswald', sans-serif;
}
.editorDiv{
    margin: 10vh 0;
    border: 1px solid black;
    box-shadow: 0px 1px 8px 3px rgba(115,106,106,1);
}
textarea{
    background-color: lightpink;
    resize: none;
}
textarea:active{
    outline: none;
}textarea:focus{
    outline: none;
}
.contentDiv{
    margin: 10vh 0;
    border: 1px solid black;
    background-color: lightpink;
    box-shadow: 0px 1px 8px 3px rgba(115,106,106,1);
}
button{
    background-color: coral;
    border: none;
}
button:hover{
    color: white;
}
button:active{
    outline: none;
}
button:focus{
    outline: none;
}